const install = (Vue, vm) => {
	vm.$u.api={}
	// 
	vm.$u.api.getIndex = (params = {}) => vm.$u.get('/api/index', params);//
	
	// 
	vm.$u.api.getGoodsList = (params = {}) => vm.$u.get(`/api/goods`,params);//
	vm.$u.api.getGoodsDetail = id => vm.$u.get(`/api/goods/${id}`);//
	vm.$u.api.getCollect = (page=1) => vm.$u.get('/api/collects',{page}) //
	vm.$u.api.isCollect = id => vm.$u.post(`/api/collects/goods/${id}`);//
	
	// 
	vm.$u.api.addCart = params => vm.$u.post(`/api/carts`,params);//
	vm.$u.api.cartGoods = () => vm.$u.get(`/api/carts?include=goods`);//
	vm.$u.api.numChange = (cart,num) => vm.$u.put(`/api/carts/${cart}`,{num});//
	vm.$u.api.delCartGoods = cart => vm.$u.delete(`/api/carts/${cart}`);//
	vm.$u.api.isCheck = cart_ids => vm.$u.patch(`/api/carts/checked`,{cart_ids});//
	
	// 
	vm.$u.api.getUserInfo = () => vm.$u.get('/api/user') //
	vm.$u.api.setUserInfo = params => vm.$u.put('/api/user',params) //
	
	// 
	vm.$u.api.authLogin = params => vm.$u.post('/api/auth/login', params)//
	vm.$u.api.authLoginOut = () => vm.$u.post('/api/auth/logout')//
	vm.$u.api.authRegister = params => vm.$u.post('/api/auth/register', params)//
	vm.$u.api.authOss = () => vm.$u.get('/api/auth/oss/token')//oss
	vm.$u.api.authAvatar = params => vm.$u.post('/api/user/avatar',params)//
	// 
	vm.$u.api.orders = params => vm.$u.post('/api/orders', params)//
	vm.$u.api.orders1 = () => vm.$u.get('/api/orders?status=1&include=goods,user,orderDetails')//
	// 
	vm.$u.api.addressList = () => vm.$u.get(`/api/address`);// 
	vm.$u.api.addressAdd = params => vm.$u.post(`/api/address`,params);// 
	vm.$u.api.addressDetail = id => vm.$u.get(`/api/address/${id}`);// 
	vm.$u.api.addressEdit = (id,params) => vm.$u.put(`/api/address/${id}`,params);// 
	vm.$u.api.addressDefault = id => vm.$u.post(`/api/address/${id}/default`);// 
	vm.$u.api.addressDel = id => vm.$u.delete(`/api/address/${id}`);// 
	
	// 
	
	vm.$u.api.addGoods1 = (id,params) => vm.$u.put(`/api/admin/goods/${id}`,params)//xiugai
	vm.$u.api.addGoods2 = () => vm.$u.get(`/api/admin/goods`)//
	vm.$u.api.addGoods3 = id => vm.$u.patch(`/api/admin/goods`,id)//
	
	vm.$u.api.getBanner = () => vm.$u.get(`/api/admin/slides`)//
	vm.$u.api.setBanner = (slide,params) => vm.$u.put(`/api/admin/slides/${slide}`,params)//
	vm.$u.api.sortBanner = (slide,seq) => vm.$u.patch(`/api/admin/slides/${slide}/seq`,{seq})//
}

export default {
	install
}
